import React, { useState, useContext } from 'react';
import { FirebaseContext } from '../../firebase';
import { doc,  getDoc, updateDoc } from 'firebase/firestore';

const Orden = ({ orden }) => {
    const [tiempoentrega, guardarTiempoEntrega] = useState(0);

    // Context de firebase
    const { firebase } = useContext(FirebaseContext);

    // Define el tiempo de entrega en tiempo real
    const definirTiempo = async id => {
        try {
            const docRef = doc(firebase.db, 'ordenes', id);
            const docSnap = await getDoc(docRef);

            if (!docSnap.exists()) {
                console.error("Documento no existe:", id);
                return;
            }
            console.log("Documento encontrado, actualizando tiempo...");

            await updateDoc(docRef, { tiempoentrega });
            console.log("Tiempo de entrega actualizado con xito.");
        } catch (error) {
            console.error("Error obteniendo o actualizando el documento:", error);
        }
    };

    // Completa el estado de una orden
    const completarOrden = async id => {
        try {
            const docRef = doc(firebase.db, 'ordenes', id);
            await updateDoc(docRef, { completado: true });
            console.log("Orden marcada como completada.");
        } catch (error) {
            console.log("Error completando la orden:", error);
        }
    };

    return (
        <div className="sm:w-1/2 lg:w-1/3 px-2 mb-4">
            <div className="p-3 shadow-md bg-white">
                <h1 className="text-yellow-600 text-lg font-bold"> {orden.id} </h1>
                {orden.orden.map(platillos => (
                    <p className="text-gray-600" key={platillos.nombre}>
                        {platillos.cantidad} {platillos.nombre}
                    </p>
                ))}

                <p className="text-gray-700 font-bold">Total a Pagar: $ {orden.total}</p>

                {orden.tiempoentrega === 0 && (
                    <div className="mb-4">
                        <label className="block text-gray-700 text-sm font-bold mb-2">
                            Tiempo de Entrega
                        </label>

                        <input
                            type="number"
                            className="shadow appearance-none border rounded w-full py-2 px-3 text-gray-700 leading-tight focus:outline-none focus:shadow-outline"
                            min="1"
                            max="20"
                            placeholder="20"
                            value={tiempoentrega}
                            onChange={e => guardarTiempoEntrega(parseInt(e.target.value))}
                        />

                        <button
                            onClick={() => definirTiempo(orden.id)}
                            type="submit"
                            className="bg-gray-800 hover:bg-gray-900 w-full mt-5 p-2 text-white uppercase font-bold"
                        >
                            Definir Tiempo
                        </button>
                    </div>
                )}

                {orden.tiempoentrega > 0 && (
                    <p className="text-gray-700">
                        Tiempo de Entrega:
                        <span className="font-bold"> {orden.tiempoentrega} Minutos</span>
                    </p>
                )}

                {!orden.completado && orden.tiempoentrega > 0 && (
                    <button
                        type="button"
                        className="bg-blue-800 hover:bg-blue-700 w-full mt-5 p-2 text-white uppercase font-bold"
                        onClick={() => completarOrden(orden.id)}
                    >
                        Marcar como lista
                    </button>
                )}
            </div>
        </div>
    );
};

export default Orden;
