import React, { useState } from 'react';
import { useNavigate } from 'react-router-dom';
import logo from '../../assets/LogoOficial.png'; // Importa el logo

const Login = () => {
  const [email, setEmail] = useState('');
  const [password, setPassword] = useState('');
  const navigate = useNavigate();

  const handleLogin = (e) => {
    e.preventDefault();
    if (email === 'user@example.com' && password === 'password123') {
      localStorage.setItem('isAuthenticated', 'true');
      navigate('/ordenes');
    } else {
      alert('Credenciales incorrectas');
    }
  };

  return (
    <div className="flex items-center justify-center min-h-screen bg-white">
      <div className="text-center w-full max-w-sm"> {/* Centra el contenido completo */}
        <img src={logo} alt="Logo" className="w-40 mx-auto mb-8" /> {/* Logo centrado */}
        <h2 className="text-3xl font-bold">Bienvenido!!</h2>
        <p className="mt-2 mb-6 text-lg">Inicia Sesin con tu cuenta</p>
        <form 
          onSubmit={handleLogin} 
          className="bg-white p-8 rounded-lg shadow-lg"
        >
          <div className="mb-4">
            <input
              type="email"
              value={email}
              onChange={(e) => setEmail(e.target.value)}
              required
              className="border border-gray-300 rounded-lg p-3 w-full text-gray-700"
              placeholder="Correo"
            />
          </div>
          <div className="mb-6">
            <input
              type="password"
              value={password}
              onChange={(e) => setPassword(e.target.value)}
              required
              className="border border-gray-300 rounded-lg p-3 w-full text-gray-700"
              placeholder="Contrasea"
            />
          </div>
          <button
        type="submit"
         style={{ backgroundColor: 'orange', color: 'white', padding: '10px', borderRadius: '5px', width: '100%', fontWeight: 'bold', marginBottom: '10px' }}
>
  Iniciar Sesin
</button>

          <button
            type="button"
            className="border border-orange-500 text-orange-500 p-3 rounded-lg w-full font-bold hover:bg-orange-100"
            onClick={() => navigate('/register')} // Navega a la pgina de registro
          >
            Registrarse
          </button>
        </form>
      </div>
    </div>
  );
};

export default Login;
