// src/App.js
import React, { useEffect } from 'react';
import { Routes, Route, useNavigate, useLocation } from 'react-router-dom';
import firebase, { FirebaseContext } from './firebase';
import Ordenes from './components/paginas/Ordenes';
import Menu from './components/paginas/Menu';
import NuevoPlatillo from './components/paginas/NuevoPlatillo';
import Login from './components/paginas/Login';
import Sidebar from './components/ui/Sidebar';
import Reparto from './components/paginas/Reparto';
import Slider from './components/paginas/Slider';
import Precio from './components/paginas/Precio';



function App() {
  const navigate = useNavigate();
  const location = useLocation();

  useEffect(() => {
    const authStatus = localStorage.getItem('isAuthenticated');
    if (authStatus !== 'true') {
      navigate('/');
    }
  }, [navigate]);

  // Condicin para mostrar o no el Sidebar y el contenedor principal
  const isLoginPage = location.pathname === '/';

  return (
    <FirebaseContext.Provider value={{ firebase }}>
      <div className={`min-h-screen ${isLoginPage ? '' : 'md:flex'}`}>
        {!isLoginPage && <Sidebar />}
        <div className={isLoginPage ? 'w-full flex justify-center items-center' : 'md:w-2/5 xl:w-4/5 p-6'}>
          <Routes>
            <Route path="/" element={<Login />} />
            <Route path="/ordenes" element={<Ordenes />} />
            <Route path="/menu" element={<Menu />} />
            <Route path="/nuevo-platillo" element={<NuevoPlatillo />} />
            <Route path= "/reparto" element={<Reparto />} />
            <Route path= "/slider" element={< Slider />} />
            <Route path= "/precio" element={< Precio />} />
            
          </Routes>
        </div>
      </div>
    </FirebaseContext.Provider>
  );
}

export default App;
