import React, { useContext, useState } from 'react';
import { useFormik } from 'formik';
import * as Yup from 'yup';
import { FirebaseContext } from '../../firebase';
import { useNavigate } from 'react-router-dom';
import { ref, uploadBytesResumable, getDownloadURL } from 'firebase/storage';
import { collection, addDoc } from 'firebase/firestore';

const NuevoPlatillo = () => {
    const [subiendo, guardarSubiendo] = useState(false);
    const [progreso, guardarProgreso] = useState(0);
    const [urlimagen, guardarUrlimagen] = useState('');

    const { firebase } = useContext(FirebaseContext);
    const navigate = useNavigate();

    const formik = useFormik({
        initialValues: {
            nombre: '',
            precio: '',
            categoria: '',
            imagen: '',
            descripcion: '',
        },
        validationSchema: Yup.object({
            nombre: Yup.string()
                .min(3, 'Los Platillos deben tener al menos 3 caracteres')
                .required('El Nombre del platillo es obligatorio'),
            precio: Yup.number()
                .min(1, 'Debes agregar un nmero')
                .required('El Precio es obligatorio'),
            categoria: Yup.string()
                .required('La categora es obligatoria'),
            descripcion: Yup.string()
                .min(10, 'La descripcin debe ser ms larga')
                .required('La descripcin es obligatoria'),
        }),
        onSubmit: async (platillo) => {
            try {
                platillo.existencia = true;
                platillo.imagen = urlimagen;
                const collectionName = getCollectionName(platillo.categoria);
                const productosCollection = collection(firebase.db, collectionName);
                await addDoc(productosCollection, platillo);
                console.log('Platillo agregado correctamente');
                navigate('/menu');
            } catch (error) {
                console.log('Error al agregar el platillo', error);
            }
        }
    });

    const handleFileChange = e => {
        const file = e.target.files[0];
        if (!file) return;

        const storageRef = ref(firebase.storage, `productos/${file.name}`);
        const uploadTask = uploadBytesResumable(storageRef, file);

        uploadTask.on(
            'state_changed',
            snapshot => {
                const progress = Math.round((snapshot.bytesTransferred / snapshot.totalBytes) * 100);
                guardarProgreso(progress);
                guardarSubiendo(true);
            },
            error => {
                guardarSubiendo(false);
                console.log(error);
            },
            () => {
                getDownloadURL(uploadTask.snapshot.ref).then(url => {
                    guardarUrlimagen(url);
                    guardarProgreso(100);
                    guardarSubiendo(false);
                });
            }
        );
    };

    const getCollectionName = (categoria) => {
        switch (categoria) {
            case 'tacos':
                return 'tacos';
            case 'bebidas':
                return 'bebidas';
            case 'carnitas_patitas':
                return 'carnitas_patitas'; // Nueva coleccin combinada
            case 'salsa':
                return 'salsa';
            default:
                return 'productos';
        }
    };

    return (
        <>
            <h1 className="text-3xl font-light mb-4">Agregar Platillo</h1>
            <div className="flex justify-center mt-10">
                <div className="w-full max-w-3xl">
                    <form onSubmit={formik.handleSubmit}>
                        <div className="mb-4">
                            <label className="block text-gray-700 text-sm font-bold mb-2" htmlFor="nombre">Nombre</label>
                            <input
                                className="shadow appearance-none border rounded w-full py-2 px-3 text-gray-700 leading-tight focus:outline-none focus:shadow-outline"
                                id="nombre"
                                type="text"
                                placeholder="Nombre Platillo"
                                value={formik.values.nombre}
                                onChange={formik.handleChange}
                                onBlur={formik.handleBlur}
                            />
                        </div>
                        {formik.touched.nombre && formik.errors.nombre && (
                            <div className="bg-red-100 border-l-4 border-red-500 text-red-700 p-4 mb-5" role="alert">
                                <p className="font-bold">Hubo un error:</p>
                                <p>{formik.errors.nombre}</p>
                            </div>
                        )}
                        <div className="mb-4">
                            <label className="block text-gray-700 text-sm font-bold mb-2" htmlFor="precio">Precio</label>
                            <input
                                className="shadow appearance-none border rounded w-full py-2 px-3 text-gray-700 leading-tight focus:outline-none focus:shadow-outline"
                                id="precio"
                                type="number"
                                placeholder="$20"
                                min="0"
                                value={formik.values.precio}
                                onChange={formik.handleChange}
                                onBlur={formik.handleBlur}
                            />
                        </div>
                        {formik.touched.precio && formik.errors.precio && (
                            <div className="bg-red-100 border-l-4 border-red-500 text-red-700 p-4 mb-5" role="alert">
                                <p className="font-bold">Hubo un error:</p>
                                <p>{formik.errors.precio}</p>
                            </div>
                        )}
                        <div className="mb-4">
                            <label className="block text-gray-700 text-sm font-bold mb-2" htmlFor="categoria">Categora</label>
                            <select
                                className="shadow appearance-none border rounded w-full py-2 px-3 text-gray-700 leading-tight focus:outline-none focus:shadow-outline"
                                id="categoria"
                                name="categoria"
                                value={formik.values.categoria}
                                onChange={formik.handleChange}
                                onBlur={formik.handleBlur}
                            >
                                <option value="">-- Seleccione --</option>
                                <option value="tacos">Tacos</option>
                                <option value="bebidas">Bebidas</option>
                                <option value="carnitas_patitas">Carnitas/Patitas</option> {/* Combinado */}
                                <option value="salsa">Salsa</option>
                            </select>
                        </div>
                        {formik.touched.categoria && formik.errors.categoria && (
                            <div className="bg-red-100 border-l-4 border-red-500 text-red-700 p-4 mb-5" role="alert">
                                <p className="font-bold">Hubo un error:</p>
                                <p>{formik.errors.categoria}</p>
                            </div>
                        )}
                        <div className="mb-4">
                            <label className="block text-gray-700 text-sm font-bold mb-2" htmlFor="imagen">Imagen</label>
                            <input
                                className="shadow appearance-none border rounded w-full py-2 px-3 text-gray-700 leading-tight focus:outline-none focus:shadow-outline"
                                id="imagen"
                                type="file"
                                onChange={handleFileChange}
                            />
                        </div>
                        <div className="mb-4">
                            {subiendo && (
                                <div className="h-12 relative w-full border">
                                    <div
                                        className="bg-green-500 absolute left-0 top-0 text-white px-2 text-sm h-12 flex items-center"
                                        style={{ width: `${progreso}%` }}
                                    >
                                        {`${progreso}%`}
                                    </div>
                                </div>
                            )}
                            {urlimagen && (
                                <p className="bg-green-500 text-white p-3 text-center my-5">
                                    La imagen se subi correctamente
                                </p>
                            )}
                        </div>
                        <div className="mb-4">
                            <label className="block text-gray-700 text-sm font-bold mb-2" htmlFor="descripcion">Descripcin</label>
                            <textarea
                                className="shadow appearance-none border rounded w-full py-2 px-3 text-gray-700 leading-tight focus:outline-none focus:shadow-outline h-40"
                                id="descripcion"
                                placeholder="Descripcin del platillo"
                                value={formik.values.descripcion}
                                onChange={formik.handleChange}
                                onBlur={formik.handleBlur}
                            ></textarea>
                        </div>
                        {formik.touched.descripcion && formik.errors.descripcion && (
                            <div className="bg-red-100 border-l-4 border-red-500 text-red-700 p-4 mb-5" role="alert">
                                <p className="font-bold">Hubo un error:</p>
                                <p>{formik.errors.descripcion}</p>
                            </div>
                        )}
                        <input
                            type="submit"
                            className="bg-gray-800 hover:bg-gray-900 w-full mt-5 p-2 text-white uppercase font-bold"
                            value="Agregar Platillo"
                        />
                    </form>
                </div>
            </div>
        </>
    );
};

export default NuevoPlatillo;
