import React, { useState, useEffect, useContext } from 'react';
import { Link } from 'react-router-dom';
import { FirebaseContext } from '../../firebase';
import { collection, onSnapshot } from 'firebase/firestore';
import Platillo from '../ui/Platillo';

const Menu = () => {
    const [platillos, guardarPlatillos] = useState([]);
    const { firebase } = useContext(FirebaseContext);

    useEffect(() => {
        const obtenerPlatillos = async () => {
            try {
                const categorias = ['tacos', 'bebidas', 'carnitas_patitas', 'salsa'];
                
                const manejarSnapshot = (snapshot, coleccion) => {
                    const nuevosPlatillos = snapshot.docs.map(doc => ({
                        id: doc.id,
                        ...doc.data(),
                        coleccion // Aadimos el nombre de la coleccin al platillo
                    }));
                    guardarPlatillos(prevPlatillos => {
                        const platillosFiltrados = prevPlatillos.filter(platillo => platillo.coleccion !== coleccion);
                        return [...platillosFiltrados, ...nuevosPlatillos];
                    });
                };

                const unsubscribeHandlers = categorias.map(categoria => {
                    const ref = collection(firebase.db, categoria);
                    return onSnapshot(ref, snapshot => manejarSnapshot(snapshot, categoria));
                });

                return () => {
                    unsubscribeHandlers.forEach(unsubscribe => unsubscribe());
                };

            } catch (error) {
                console.error("Error al obtener los platillos:", error);
            }
        };
        obtenerPlatillos();
    }, [firebase]);

    // Agrupamos los platillos por categora
    const platillosPorCategoria = categorias => {
        return categorias.reduce((acc, categoria) => {
            acc[categoria] = platillos.filter(platillo => platillo.coleccion === categoria);
            return acc;
        }, {});
    };

    const platillosAgrupados = platillosPorCategoria(['tacos', 'bebidas', 'carnitas_patitas', 'salsa']);

    return (
        <>
            <h1 className='text-3xl font-light mb-4'>Men</h1>
            <Link to="/nuevo-platillo" className='bg-yellow-950 hover:bg-blue-700 inline-block mb-5 p-2 text-white uppercase font-bold'>
                Agregar Platillo
            </Link>

            {Object.keys(platillosAgrupados).map(categoria => (
                <div key={categoria}>
                    <h2 className='text-2xl font-light mb-4 capitalize'>{categoria.replace('_', ' ')}</h2>
                    {platillosAgrupados[categoria].map(platillo => (
                        <Platillo
                            key={platillo.id}
                            platillo={platillo}
                        />
                    ))}
                </div>
            ))}
        </>
    );
}

export default Menu;
