import React, { useState, useEffect } from 'react';
import { useFormik } from 'formik';
import * as Yup from 'yup';
import { FirebaseContext } from '../../firebase';
import { collection, addDoc, getDocs, updateDoc, doc, deleteDoc } from 'firebase/firestore';
import { ref, uploadBytesResumable, getDownloadURL } from 'firebase/storage';

const Reparto = () => {
    const { firebase } = React.useContext(FirebaseContext);
    const [image, setImage] = useState(null);
    const [uploading, setUploading] = useState(false);
    const [repartidores, setRepartidores] = useState([]);

    useEffect(() => {
        const obtenerRepartidores = async () => {
            const querySnapshot = await getDocs(collection(firebase.db, 'repartidores'));
            const repartidoresLista = querySnapshot.docs.map(doc => ({ id: doc.id, ...doc.data() }));
            setRepartidores(repartidoresLista);
        };
        obtenerRepartidores();
    }, [firebase.db]);

    const handleImageChange = (e) => {
        if (e.target.files[0]) {
            setImage(e.target.files[0]);
        }
    };

    const formik = useFormik({
        initialValues: {
            nombre: '',
            apellido: '',
            telefono: '',
        },
        validationSchema: Yup.object({
            nombre: Yup.string().required('El nombre es obligatorio'),
            apellido: Yup.string().required('El apellido es obligatorio'),
            telefono: Yup.string()
                .matches(/^[0-9]+$/, 'El telfono solo puede contener nmeros')
                .required('El nmero de telfono es obligatorio'),
        }),
        onSubmit: async (repartidor) => {
            if (image) {
                const storageRef = ref(firebase.storage, `repartidores/${image.name}`);
                const uploadTask = uploadBytesResumable(storageRef, image);

                setUploading(true);

                uploadTask.on(
                    'state_changed',
                    (snapshot) => {
                        const progress = (snapshot.bytesTransferred / snapshot.totalBytes) * 100;
                        console.log(`Progreso: ${progress}%`);
                    },
                    (error) => {
                        console.log('Error al subir la imagen:', error);
                        setUploading(false);
                        alert('Error al subir la imagen. Intntalo de nuevo.');
                    },
                    async () => {
                        const downloadURL = await getDownloadURL(uploadTask.snapshot.ref);
                        const repartidorConImagen = { 
                            ...repartidor, 
                            fotoURL: downloadURL, 
                            disponible: true 
                        };

                        try {
                            const repartidoresCollection = collection(firebase.db, 'repartidores');
                            await addDoc(repartidoresCollection, repartidorConImagen);
                            console.log('Repartidor agregado correctamente');
                            setRepartidores([...repartidores, repartidorConImagen]);
                            alert('Los datos del repartidor se subieron correctamente.');
                        } catch (error) {
                            console.log('Error al agregar el repartidor:', error.message);
                            alert('Error al subir los datos del repartidor. Intntalo de nuevo.');
                        } finally {
                            setUploading(false);
                        }
                    }
                );
            } else {
                alert('Por favor, selecciona una imagen para subir.');
            }
        }
    });

    const cambiarDisponibilidad = async (id, disponible) => {
        const repartidorRef = doc(firebase.db, 'repartidores', id);
        await updateDoc(repartidorRef, { disponible: !disponible });
        setRepartidores(repartidores.map(rep => rep.id === id ? { ...rep, disponible: !disponible } : rep));
    };

    const eliminarRepartidor = async (id) => {
        const repartidorRef = doc(firebase.db, 'repartidores', id);
        await deleteDoc(repartidorRef);
        setRepartidores(repartidores.filter(rep => rep.id !== id));
    };

    return (
        <>
            <h1 className="text-3xl font-light mb-4">Agregar Repartidor</h1>
            <form onSubmit={formik.handleSubmit}>
                <div className="mb-4">
                    <label className="block text-gray-700 text-sm font-bold mb-2" htmlFor="nombre">Nombre</label>
                    <input
                        id="nombre"
                        type="text"
                        className="shadow appearance-none border rounded w-full py-2 px-3 text-gray-700 leading-tight focus:outline-none focus:shadow-outline"
                        value={formik.values.nombre}
                        onChange={formik.handleChange}
                        onBlur={formik.handleBlur}
                    />
                    {formik.touched.nombre && formik.errors.nombre ? (
                        <div className="bg-red-100 border-l-4 border-red-500 text-red-700 p-4 mb-5" role="alert">
                            <p>{formik.errors.nombre}</p>
                        </div>
                    ) : null}
                </div>

                <div className="mb-4">
                    <label className="block text-gray-700 text-sm font-bold mb-2" htmlFor="apellido">Apellido</label>
                    <input
                        id="apellido"
                        type="text"
                        className="shadow appearance-none border rounded w-full py-2 px-3 text-gray-700 leading-tight focus:outline-none focus:shadow-outline"
                        value={formik.values.apellido}
                        onChange={formik.handleChange}
                        onBlur={formik.handleBlur}
                    />
                    {formik.touched.apellido && formik.errors.apellido ? (
                        <div className="bg-red-100 border-l-4 border-red-500 text-red-700 p-4 mb-5" role="alert">
                            <p>{formik.errors.apellido}</p>
                        </div>
                    ) : null}
                </div>

                <div className="mb-4">
                    <label className="block text-gray-700 text-sm font-bold mb-2" htmlFor="telefono">Telfono</label>
                    <input
                        id="telefono"
                        type="text"
                        className="shadow appearance-none border rounded w-full py-2 px-3 text-gray-700 leading-tight focus:outline-none focus:shadow-outline"
                        value={formik.values.telefono}
                        onChange={formik.handleChange}
                        onBlur={formik.handleBlur}
                    />
                    {formik.touched.telefono && formik.errors.telefono ? (
                        <div className="bg-red-100 border-l-4 border-red-500 text-red-700 p-4 mb-5" role="alert">
                            <p>{formik.errors.telefono}</p>
                        </div>
                    ) : null}
                </div>

                <div className="mb-4">
                    <label className="block text-gray-700 text-sm font-bold mb-2" htmlFor="fotoPerfil">Foto de Perfil</label>
                    <input
                        id="fotoPerfil"
                        type="file"
                        className="shadow appearance-none border rounded w-full py-2 px-3 text-gray-700 leading-tight focus:outline-none focus:shadow-outline"
                        onChange={handleImageChange}
                    />
                </div>

                <input
                    type="submit"
                    className="bg-gray-800 hover:bg-gray-900 w-full mt-5 p-2 text-white uppercase font-bold"
                    value={uploading ? "Cargando..." : "Agregar Repartidor"}
                    disabled={uploading}
                />
            </form>

            <h2 className="text-2xl font-light mt-10">Lista de Repartidores</h2>
            <ul>
                {repartidores.map(repartidor => (
                    <li key={repartidor.id} className="border p-4 mb-4 relative">
                        <p><strong>Nombre:</strong> {repartidor.nombre} {repartidor.apellido}</p>
                        <p><strong>Telfono:</strong> {repartidor.telefono}</p>
                        <p><strong>Disponible:</strong> {repartidor.disponible ? 'S' : 'No'}</p>
                        {repartidor.fotoURL && (
                          <img
                          src={repartidor.fotoURL}
                          alt="Foto de perfil"
                          className="w-20 h-20 object-cover rounded-full border-2 border-gray-300 mb-4"
                      />
                       )} 

                        <div className="flex space-x-4 mt-4">
                            <button
                                className={`flex-1 ${repartidor.disponible ? 'bg-green-500 hover:bg-green-700' : 'bg-orange-500 hover:bg-orange-700'} text-white font-bold py-2 px-4`}
                                onClick={() => cambiarDisponibilidad(repartidor.id, repartidor.disponible)}
                            >
                                {repartidor.disponible ? 'Marcar como No Disponible' : 'Marcar como Disponible'}
                            </button>

                            <button
                                className="bg-red-500 hover:bg-red-700 text-white font-bold py-2 px-4"
                                onClick={() => eliminarRepartidor(repartidor.id)}
                            >
                                Eliminar
                            </button>
                        </div>
                    </li>
                ))}
            </ul>
        </>
    );
};

export default Reparto;
